"use client"

import type React from "react"
import { useEffect, useMemo, useState } from "react"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger, DialogFooter } from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Checkbox } from "@/components/ui/checkbox"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover"
import { Calendar } from "@/components/ui/calendar"
import { Loader2, CalendarIcon, Plus, Trash2, FileText, Users2, Shield } from "lucide-react"
import { FormField } from "@/components/ui/field"
import { useToast } from "@/hooks/use-toast"
import { useAuth } from "@/components/auth-provider"
import { usersApi, followupsApi, rolesApi } from "@/lib/api"
import { format } from "date-fns"
import { ptBR } from "date-fns/locale"

// Helpers ------------------------------------------------------------------
function pad(n: number) { return String(n).padStart(2, "0") }
function toLocalDateTimeISO(d: Date, time: string | undefined) {
  let base = new Date(d)
  if (time) {
    const [hh, mm] = time.split(":").map(Number)
    base.setHours(hh ?? 0, mm ?? 0, 0, 0)
  }
  return `${base.getFullYear()}-${pad(base.getMonth() + 1)}-${pad(base.getDate())}T${pad(base.getHours())}:${pad(base.getMinutes())}:${pad(base.getSeconds())}`
}

// Enums espelhando o backend -----------------------------------------------
const MEETING_STATUS = ["SCHEDULED", "COMPLETED", "CANCELLED", "RESCHEDULED"] as const
const MEETING_TYPE = [
  "SPIRITUAL_DIRECTION",
  "VOCATIONAL_ACCOMPANIMENT",
  "FORMATIVE_EVALUATION",
  "PERSONAL_DEVELOPMENT",
  "COMMUNITY_INTEGRATION",
  "ACADEMIC_FOLLOW_UP",
  "PASTORAL_SUPERVISION",
  "OTHER",
] as const
const VISIBILITY = ["PRIVATE", "SHARED_SPECIFIC", "SHARED_ROLE", "COORDINATION"] as const

type MeetingStatus = (typeof MEETING_STATUS)[number]
type MeetingType = (typeof MEETING_TYPE)[number]
type Visibility = (typeof VISIBILITY)[number]

// Componente reutilizvel de data + hora (evita sobreposio)
function DateTimeGroup({
  idPrefix,
  date,
  time,
  onDate,
  onTime,
  buttonLabel,
  required,
}: {
  idPrefix: string
  date: Date | undefined
  time: string
  onDate: (d: Date | undefined) => void
  onTime: (t: string) => void
  buttonLabel: string
  required?: boolean
}) {
  return (
    <div className="flex flex-wrap gap-2 items-center">
      <Popover>
        <PopoverTrigger asChild>
          <Button
            id={`${idPrefix}-date`}
            variant="outline"
            className="flex-1 min-w-[12rem] justify-start text-left font-normal bg-transparent"
          >
            <CalendarIcon className="mr-2 h-4 w-4" />
            {date ? format(date, "PPP", { locale: ptBR }) : buttonLabel}
          </Button>
        </PopoverTrigger>
        <PopoverContent className="w-auto p-0">
          <Calendar mode="single" selected={date} onSelect={(d) => onDate(d || undefined)} initialFocus />
        </PopoverContent>
      </Popover>
      <div className="w-32">
        <Label htmlFor={`${idPrefix}-time`} className="sr-only">Hora</Label>
        <Input
          id={`${idPrefix}-time`}
          type="time"
          step={60}
          value={time}
          onChange={(e) => onTime(e.target.value)}
          disabled={!date}
          placeholder="HH:mm"
          aria-required={required}
        />
      </div>
    </div>
  )
}

export default function FollowupsPage() {
  const { user } = useAuth()
  const { toast } = useToast()

  const [followups, setFollowups] = useState<any[]>([])
  const [users, setUsers] = useState<any[]>([])
  const [roles, setRoles] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [dialogOpen, setDialogOpen] = useState(false)
  const [editingId, setEditingId] = useState<number | null>(null)

  const [formData, setFormData] = useState({
    userId: "", // menteeId
    title: "",
    scheduledDate: new Date(),
    scheduledTime: "09:00",
    actualDate: undefined as Date | undefined,
    actualTime: "",

    status: "SCHEDULED" as MeetingStatus,
    meetingType: "OTHER" as MeetingType,

    content: "",
    objectives: "",
    discussionPoints: "",
    commitments: "",
    nextSteps: "",
    mentorNotes: "",

    visibility: "PRIVATE" as Visibility,
    sharedWithUserIds: [] as number[],
    sharedRoleIds: [] as number[],
  })

  const isAdmin = user?.role === "ADMIN"

  useEffect(() => {
    fetchFollowups()
    if (isAdmin) {
      fetchUsers()
      fetchRoles()
    }
  }, [isAdmin])

  const fetchFollowups = async () => {
    try {
      const data = await followupsApi.getAccessible()
      setFollowups(data)
    } catch {
      toast({ title: "Erro ao carregar acompanhamentos", variant: "destructive" })
    } finally {
      setLoading(false)
    }
  }

  const fetchUsers = async () => {
    try {
      const data = await usersApi.getAll()
      setUsers(data)
    } catch {
      toast({ title: "Erro ao carregar usurios", variant: "destructive" })
    }
  }

  const fetchRoles = async () => {
    try {
      const data = await rolesApi.getAll?.()
      if (data) setRoles(data)
    } catch {
      // opcional
    }
  }

  const resetForm = () => {
  setFormData({
    userId: "",
    title: "",
    scheduledDate: new Date(),
    scheduledTime: "09:00",
    actualDate: undefined,
    actualTime: "",
    status: "SCHEDULED",
    meetingType: "OTHER",
    content: "",
    objectives: "",
    discussionPoints: "",
    commitments: "",
    nextSteps: "",
    mentorNotes: "",
    visibility: "PRIVATE",
    sharedWithUserIds: [],
    sharedRoleIds: [],
  })
  setEditingId(null)
}

const prefillForEdit = (item: any) => {
  const toTime = (d?: string) => {
    if (!d) return ""
    const date = new Date(d)
    const hh = String(date.getHours()).padStart(2, "0")
    const mm = String(date.getMinutes()).padStart(2, "0")
    return `${hh}:${mm}`
  }

  setFormData({
    userId: String(item.menteeId ?? item.mentee?.id ?? ""),
    title: item.title ?? "",
    scheduledDate: item.scheduledDate ? new Date(item.scheduledDate) : new Date(),
    scheduledTime: toTime(item.scheduledDate) || "09:00",
    actualDate: item.actualDate ? new Date(item.actualDate) : undefined,
    actualTime: toTime(item.actualDate),
    status: item.status ?? "SCHEDULED",
    meetingType: item.meetingType ?? "OTHER",
    content: item.content ?? "",
    objectives: item.objectives ?? "",
    discussionPoints: item.discussionPoints ?? "",
    commitments: item.commitments ?? "",
    nextSteps: item.nextSteps ?? "",
    mentorNotes: item.mentorNotes ?? "",
    visibility: item.visibility ?? "PRIVATE",
    sharedWithUserIds: item.sharedWithUserIds ?? [],
    sharedRoleIds: item.sharedRoleIds ?? [],
  })
  setEditingId(item.id)
  setDialogOpen(true)
}

  const isSubmitDisabled = useMemo(() => {
    return !formData.userId || !formData.title || !formData.scheduledDate || !formData.scheduledTime
  }, [formData.userId, formData.title, formData.scheduledDate, formData.scheduledTime])

  const handleSubmit: React.FormEventHandler<HTMLFormElement> = async (e) => {
    e.preventDefault()

    const menteeId = Number(formData.userId)
    if (!menteeId) {
      toast({ title: "Selecione um usurio", variant: "destructive" })
      return
    }

    const dto = {
      menteeId,
      title: formData.title,
      scheduledDate: toLocalDateTimeISO(formData.scheduledDate, formData.scheduledTime),
      actualDate: formData.actualDate ? toLocalDateTimeISO(formData.actualDate, formData.actualTime || "00:00") : null,
      status: formData.status,
      meetingType: formData.meetingType,
      content: formData.content || null,
      objectives: formData.objectives || null,
      discussionPoints: formData.discussionPoints || null,
      commitments: formData.commitments || null,
      nextSteps: formData.nextSteps || null,
      mentorNotes: formData.mentorNotes || null,
      visibility: formData.visibility,
      sharedWithUserIds: formData.sharedWithUserIds,
      sharedRoleIds: formData.sharedRoleIds,
    }

    try {
      if (editingId) {
        await followupsApi.update?.(editingId, dto)
      } else {
        await followupsApi.create(dto)
      }
      toast({ title: editingId ? "Acompanhamento atualizado com sucesso" : "Acompanhamento criado com sucesso" })
      setDialogOpen(false)
      resetForm()
      fetchFollowups()
    } catch (err) {
      toast({ title: "Erro ao criar acompanhamento", description: (err as any)?.message, variant: "destructive" })
    }
  }

  const toggleArraySelection = (arr: number[], id: number) => {
    return arr.includes(id) ? arr.filter((x) => x !== id) : [...arr, id]
  }

  // Memo para exibir rtulos legveis
  const meetingTypeLabel = useMemo(() => ({
    SPIRITUAL_DIRECTION: "Direo Espiritual",
    VOCATIONAL_ACCOMPANIMENT: "Acompanhamento Vocacional",
    FORMATIVE_EVALUATION: "Avaliao Formativa",
    PERSONAL_DEVELOPMENT: "Desenvolvimento Pessoal",
    COMMUNITY_INTEGRATION: "Integrao Comunitria",
    ACADEMIC_FOLLOW_UP: "Acompanhamento Acadmico",
    PASTORAL_SUPERVISION: "Superviso Pastoral",
    OTHER: "Outro",
  } as Record<MeetingType, string>), [])

  const statusLabel = useMemo(() => ({
    SCHEDULED: "Agendado",
    COMPLETED: "Realizado",
    CANCELLED: "Cancelado",
    RESCHEDULED: "Remarcado",
  } as Record<MeetingStatus, string>), [])

  const visibilityLabel = useMemo(() => ({
    PRIVATE: "Privado  apenas Formador",
    SHARED_SPECIFIC: "Compartilhado  Usurios especficos",
    SHARED_ROLE: "Compartilhado  por Funo",
    COORDINATION: "Coordenao  Coordenadores",
  } as Record<Visibility, string>), [])

  if (loading) {
    return (
      <div className="flex h-full items-center justify-center">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
      </div>
    )
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">
            {isAdmin ? "Gesto de Acompanhamentos" : "Meus Acompanhamentos"}
          </h1>
          <p className="text-muted-foreground">
            {isAdmin ? "Gerencie os acompanhamentos formativos dos membros" : "Acompanhe seus registros e acompanhamentos realizados"}
          </p>
        </div>

        {isAdmin && (
          <Dialog open={dialogOpen} onOpenChange={(open) => { setDialogOpen(open); if (open) resetForm() }}>
            <DialogTrigger asChild>
              <Button>
                <Plus className="mr-2 h-4 w-4" />
                Novo Acompanhamento
              </Button>
            </DialogTrigger>

            <DialogContent className="max-w-[95vw] w-[1100px] max-h-[85vh] overflow-y-auto overflow-x-hidden">
              <DialogHeader className="pb-0">
                <DialogTitle>{editingId ? "Editar Acompanhamento" : "Novo Acompanhamento"}</DialogTitle>
              </DialogHeader>

              <form onSubmit={handleSubmit} className="space-y-4">
                {/* Mentee */}
                <FormField label="Usurio (mentorado)" htmlFor="userId" required>
                  <Select value={formData.userId} onValueChange={(value) => setFormData({ ...formData, userId: value })}>
                    <SelectTrigger id="userId">
                      <SelectValue placeholder="Selecione um usurio" />
                    </SelectTrigger>
                    <SelectContent>
                      {users.map((u) => (
                        <SelectItem key={u.id} value={String(u.id)}>
                          {u.name} ({u.username})
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </FormField>

                {/* Ttulo */}
                <FormField label="Ttulo" htmlFor="title" required>
                  <Input id="title" value={formData.title} onChange={(e) => setFormData({ ...formData, title: e.target.value })} placeholder="Ex: Caminho vocacional" />
                </FormField>

                {/* Tipo, Status, Visibilidade */}
                <div className="grid grid-cols-1 md:grid-cols-3 gap-3">
                  <FormField label="Tipo do Encontro" htmlFor="meetingType" required>
                    <Select value={formData.meetingType} onValueChange={(v: MeetingType) => setFormData({ ...formData, meetingType: v })}>
                      <SelectTrigger id="meetingType"><SelectValue placeholder="Selecione" /></SelectTrigger>
                      <SelectContent>
                        {MEETING_TYPE.map((t) => (
                          <SelectItem key={t} value={t}>{meetingTypeLabel[t]}</SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </FormField>

                  <FormField label="Status" htmlFor="status" required>
                    <Select value={formData.status} onValueChange={(v: MeetingStatus) => setFormData({ ...formData, status: v })}>
                      <SelectTrigger id="status"><SelectValue placeholder="Selecione" /></SelectTrigger>
                      <SelectContent>
                        {MEETING_STATUS.map((s) => (
                          <SelectItem key={s} value={s}>{statusLabel[s]}</SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </FormField>

                  <FormField label="Visibilidade" htmlFor="visibility" required>
                    <Select value={formData.visibility} onValueChange={(v: Visibility) => setFormData({ ...formData, visibility: v })}>
                      <SelectTrigger id="visibility"><SelectValue placeholder="Selecione" /></SelectTrigger>
                      <SelectContent>
                        {VISIBILITY.map((v) => (
                          <SelectItem key={v} value={v}>{visibilityLabel[v]}</SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </FormField>
                </div>

                {/* Datas e horrios (componente que evita sobreposio) */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                  <FormField label="Data agendada" htmlFor="scheduledDate" required>
                    <DateTimeGroup
                      idPrefix="scheduled"
                      date={formData.scheduledDate}
                      time={formData.scheduledTime}
                      onDate={(d) => setFormData({ ...formData, scheduledDate: d || new Date() })}
                      onTime={(t) => setFormData({ ...formData, scheduledTime: t })}
                      buttonLabel="Selecione uma data"
                      required
                    />
                  </FormField>

                  <FormField label="Data realizada (opcional)" htmlFor="actualDate">
                    <DateTimeGroup
                      idPrefix="actual"
                      date={formData.actualDate}
                      time={formData.actualTime}
                      onDate={(d) => setFormData({ ...formData, actualDate: d })}
                      onTime={(t) => setFormData({ ...formData, actualTime: t })}
                      buttonLabel="Selecione uma data"
                    />
                  </FormField>
                </div>

                {/* Textos longos */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                  <FormField label="Contedo / Notas" htmlFor="content">
                    <Textarea id="content" value={formData.content} onChange={(e) => setFormData({ ...formData, content: e.target.value })} placeholder="Contedo abordado durante o encontro" className="min-h-24" />
                  </FormField>
                  <FormField label="Objetivos" htmlFor="objectives">
                    <Textarea id="objectives" value={formData.objectives} onChange={(e) => setFormData({ ...formData, objectives: e.target.value })} placeholder="Metas e objetivos do acompanhamento" className="min-h-24" />
                  </FormField>
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                  <FormField label="Pontos discutidos" htmlFor="discussionPoints">
                    <Textarea id="discussionPoints" value={formData.discussionPoints} onChange={(e) => setFormData({ ...formData, discussionPoints: e.target.value })} placeholder="Tpicos principais discutidos" className="min-h-24" />
                  </FormField>
                  <FormField label="Compromissos" htmlFor="commitments">
                    <Textarea id="commitments" value={formData.commitments} onChange={(e) => setFormData({ ...formData, commitments: e.target.value })} placeholder="Aes assumidas" className="min-h-24" />
                  </FormField>
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                  <FormField label="Prximos passos" htmlFor="nextSteps">
                    <Textarea id="nextSteps" value={formData.nextSteps} onChange={(e) => setFormData({ ...formData, nextSteps: e.target.value })} placeholder="O que fazer a seguir" className="min-h-24" />
                  </FormField>
                  <FormField label="Observaes do formador" htmlFor="mentorNotes">
                    <Textarea id="mentorNotes" value={formData.mentorNotes} onChange={(e) => setFormData({ ...formData, mentorNotes: e.target.value })} placeholder="Anotaes privadas do formador" className="min-h-24" />
                  </FormField>
                </div>

                {/* Compartilhamentos condicionais */}
                {formData.visibility === "SHARED_SPECIFIC" && (
                  <div className="rounded-lg border p-4">
                    <div className="flex items-center gap-2 mb-3">
                      <Users2 className="h-4 w-4" />
                      <span className="font-medium">Compartilhar com usurios especficos</span>
                    </div>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-2 max-h-56 overflow-auto">
                      {users.map((u) => (
                        <label key={u.id} className="flex items-center gap-2 text-sm">
                          <Checkbox
                            checked={formData.sharedWithUserIds.includes(u.id)}
                            onCheckedChange={() => setFormData({ ...formData, sharedWithUserIds: toggleArraySelection(formData.sharedWithUserIds, u.id) })}
                          />
                          {u.name} ({u.username})
                        </label>
                      ))}
                    </div>
                  </div>
                )}

                {formData.visibility === "SHARED_ROLE" && (
                  <div className="rounded-lg border p-4">
                    <div className="flex items-center gap-2 mb-3">
                      <Shield className="h-4 w-4" />
                      <span className="font-medium">Compartilhar por funo</span>
                    </div>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-2 max-h-56 overflow-auto">
                      {roles.map((r) => (
                        <label key={r.id} className="flex items-center gap-2 text-sm">
                          <Checkbox
                            checked={formData.sharedRoleIds.includes(r.id)}
                            onCheckedChange={() => setFormData({ ...formData, sharedRoleIds: toggleArraySelection(formData.sharedRoleIds, r.id) })}
                          />
                          {r.name}
                        </label>
                      ))}
                    </div>
                  </div>
                )}

                <DialogFooter className="pt-2">
                  <Button type="button" variant="outline" onClick={() => setDialogOpen(false)}>Cancelar</Button>
                  <Button type="submit" disabled={isSubmitDisabled}>Salvar</Button>
                </DialogFooter>
              </form>
            </DialogContent>
          </Dialog>
        )}
      </div>

      {/* LISTAGEM */}
      {followups.length === 0 ? (
        <Card>
          <CardContent className="flex flex-col items-center justify-center py-12">
            <FileText className="mb-4 h-12 w-12 text-muted-foreground" />
            <p className="text-muted-foreground">Nenhum acompanhamento {isAdmin ? "cadastrado" : "encontrado"}</p>
          </CardContent>
        </Card>
      ) : (
        <div className="space-y-4">
          {followups.map((f) => {
            // Disponvel para o usurio que criou (mentor)
            const canEdit = user && (user.id === f.mentorId || user.username === f.mentorUsername)

            return (
              <Card key={f.id}>
                <CardHeader className="pb-2">
                  <div className="flex items-start justify-between gap-4">
                    <div>
                      <CardTitle>{f.title || "Acompanhamento"}</CardTitle>
                      <CardDescription className="mt-1">
                        {f.mentorNotes || f.content || "Sem observaes."}
                      </CardDescription>
                    </div>

                    <div className="flex items-center gap-2">
                      {canEdit && (
                        <Button size="sm" variant="outline" onClick={() => prefillForEdit(f)}>
                          Editar
                        </Button>
                      )}
                      {isAdmin && (
                        <Button
                          variant="ghost"
                          size="icon"
                          onClick={async () => {
                            if (!confirm("Tem certeza que deseja excluir este acompanhamento?")) return
                            try {
                              await followupsApi.delete(f.id)
                              toast({ title: "Acompanhamento excludo com sucesso" })
                              fetchFollowups()
                            } catch {
                              toast({ title: "Erro ao excluir acompanhamento", variant: "destructive" })
                            }
                          }}
                        >
                          <Trash2 className="h-4 w-4 text-destructive" />
                        </Button>
                      )}
                    </div>
                  </div>
                </CardHeader>

                <CardContent>
                  <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-x-8 gap-y-2 text-sm">
                    {isAdmin && f.menteeName && (
                      <div>
                        <span className="text-muted-foreground">Usurio:</span>{" "}
                        <span className="font-medium">{f.menteeName}</span>
                      </div>
                    )}

                    {f.meetingType && (
                      <div>
                        <span className="text-muted-foreground">Tipo:</span>{" "}
                        <span className="font-medium">{(meetingTypeLabel as any)[f.meetingType] || f.meetingType}</span>
                      </div>
                    )}

                    {f.status && (
                      <div className="flex items-center gap-2">
                        <span className="text-muted-foreground">Status:</span>
                        <Badge variant="secondary">{(statusLabel as any)[f.status] || f.status}</Badge>
                      </div>
                    )}

                    {f.visibility && (
                      <div className="flex items-center gap-2">
                        <span className="text-muted-foreground">Visibilidade:</span>
                        <Badge variant="outline">{(visibilityLabel as any)[f.visibility] || f.visibility}</Badge>
                      </div>
                    )}

                    <div>
                      <span className="text-muted-foreground">Agendado:</span>{" "}
                      <span className="font-medium">
                        {f.scheduledDate ? new Date(f.scheduledDate).toLocaleString("pt-BR") : "N/A"}
                      </span>
                    </div>

                    {f.actualDate && (
                      <div>
                        <span className="text-muted-foreground">Realizado:</span>{" "}
                        <span className="font-medium">{new Date(f.actualDate).toLocaleString("pt-BR")}</span>
                      </div>
                    )}
                  </div>
                </CardContent>
              </Card>
            )
          })}
        </div>
      )}
    </div>
  )
}
